import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasAdministradoraMHO } from '../../../../dependencies/administradoraMHODependency';
import { CLASS_NAME, HistoricoPagarMHO } from '../../../../models/dtos/mho/historicoPagarMHO';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const HistoricoPagarMHOSearchView: FC<SearchViewProps<HistoricoPagarMHO>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção de Histórico de Contas a Pagar"
      dependencies={[() => hasAdministradoraMHO(globalReducer.globalParameter)]}
      wikiId={1109}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'HistoricoPagarMHOEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default HistoricoPagarMHOSearchView;
